<template>
  <div class="login bg-lightWhite h-full lg:h-screen">
        <svg viewBox="0 0 1656 320" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M455.351 149.061C1145.09 -27.4895 1536 66 1656 127.5L1656 0.0869791L0.49982 0.0869407L0.499832 204C0.499832 204 222.152 208.752 455.351 149.061Z" fill="#1A3F93"/>
        </svg>
        <div class="h-screen relative lg:absolute w-full top-0">
            <div class="relative lg:top-0 lg:right-0 p-12 lg:p-2 w-full lg:w-1/2 mx-auto h-screen flex flex-col justify-center">
                <div class="py-12 text-center rounded-lg">
                    <img class="mx-auto w-81" alt="Logo" src="../assets/logo.svg">
                    <form @keypress.enter="submitForm" @submit.prevent="submitForm" class="w-full lg:w-2/4 mx-auto">
                        <div class="mb-8 relative">
                            <input type="email" v-model="email" placeholder="Email" class="bg-lightWhite focus:outline-none p-2 border rounded focus:border-mainBlue focus:placeholder-mainBlue w-full focus:text-mainBlue transition" :class="(showusererror || logmessage) ? 'border-red-500 placeholder-red-500 text-red-500' : 'border-gray-400 placeholder-grey-400'" @input="hideError">
                            <span class="my-2 block text-left text-red-600" v-if="showusererror">Your Email is Invalid</span>
                        </div>
                        <div class="mb-8 relative">
                            <input type="password" v-model="password" placeholder="Password" class="bg-lightWhite focus:outline-none p-2 rounded border focus:border-mainBlue focus:text-mainBlue focus:placeholder-mainBlue w-full transition" :class="(showatuherror || logmessage) ? 'border-red-500 placeholder-red-500 text-red-500' : 'border-gray-400 placeholder-grey-400 text-mainBlue'" @input="hideError">
                            <span class="my-2 block text-left text-red-500" v-if="showatuherror">Your Password is Invalid</span>
                        </div>
                        <div v-if="logerror" class="flex items-center bg-red-500 text-white text-sm font-bold px-4 py-3 mb-8 rounded" role="alert">
                          <svg viewBox="0 0 24 24" width="24" height="24" stroke="currentColor" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round" class="css-i6dzq1">
                              <polygon points="7.86 2 16.14 2 22 7.86 22 16.14 16.14 22 7.86 22 2 16.14 2 7.86 7.86 2"></polygon>
                              <line x1="12" y1="8" x2="12" y2="12"></line>
                              <line x1="12" y1="16" x2="12.01" y2="16"></line>
                          </svg>
                          <p class="ml-4">{{logmessage}}</p>
                        </div>
                        <div class="relative">
                            <button type="submit" value="submit" class="btn-submit bg-mainBlue text-white py-2 px-12 rounded focus:outline-none transition">Login</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// @ is an alias to /src
import firebase from '../db';
export default {
  name: 'Login',
  data() {
    return {
      email: '',
      password: '',
      logmessage: false,
      showusererror: false,
      showatuherror: false,
      logerror: false,
    }
  },
  methods: {
    submitForm() {
      if (this.emptyInput()) {
        this.$store.dispatch('login', {email: this.email.toLowerCase(), password: this.password})
        .then((resp) => {
            this.$router.push('/dashboard').catch(err => {})
        })
        .catch(err => {
            this.logerror = true
            this.logmessage = err.message
            this.showusererror = false
            this.showatuherror = false
        })
      }
    },
    emptyInput() {
      if (!this.email) {
        this.showusererror = true
        this.showatuherror = false
        return false
      }
      else if (!this.password) {
        this.showusererror = false
        this.showatuherror = true
        return false
      }
      else
        return true
    },
    hideError() {
      this.showusererror = false
      this.showatuherror = false
      this.logerror = false
    },
  },
  created() {
    const isAuth = firebase.auth().currentUser
    if(isAuth)
      this.$router.push('/dashboard')
  },
}
</script>

<style scoped>
.br-1 {
    top: 10px;
    right: 10px;
}
</style>